/* What the animation consists of */
@-webkit-keyframes particleAnimation
{
from {
        left: -100px;
}
to {
        left: calc( 100% + 100px );
}
}
@keyframes particleAnimation
{
from {
        left: -100px;
}
to {
        left: calc( 100% + 100px );
}
}
/* Initiate the point*/
.p{
  position:fixed;
  left:0px;
  top:50px;
  width:1px;
  height:1px;
  background-color:white;
  position:fixed;
  -webkit-animation-name:particleAnimation;
          animation-name:particleAnimation;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.p::before{
  position:absolute;
  display:block;
  content:"";
  width:100px;
  right:1px;
  top:0px;
  height:1px;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0,0,0,0)),to(rgba(255,255,255,0.4)));
  background: linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(255,255,255,0.4) 100%);
}
/*Give each point a different duration*/
.p-1{
  -webkit-animation-duration:10s;
          animation-duration:10s;
}
.p-2{
  -webkit-animation-duration:5s;
          animation-duration:5s;
  top:30%;
}
.p-3{
  -webkit-animation-duration:18s;
          animation-duration:18s;
  top:60%;
}
.p-4{
  -webkit-animation-duration:8s;
          animation-duration:8s;
  top:90%;
}

